




















import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyTimeRangePicker"
})
export default class MyTimeRangePicker extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    get starTime() {
        return (this.currentValue && this.currentValue[0]) || "";
    }
    get endTime() {
        return (this.currentValue && this.currentValue[1]) || "";
    }
}
